.features {
  margin-top: -160px;
  min-height: 400px;
  background-color: red;
  position: relative;
  overflow: hidden;
  margin-bottom: 500px;
  z-index: -1;

  &__bcg {
    width: 30%;
    position: absolute;
    right: 0;
    top: 0;
    clip-path: polygon(25% 0%, 100% 0%, 75% 100%, 0% 100%);
    height: 100%;

    &-img {
      height: 100%;
    }
  }

  &__title {
    margin-bottom: 69px;
  }

  &__icon {
    width: 64px;
    height: 64px;
    background-color: #fff;
  }
}
